body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
    font-size: 13px;
}

.section {
    margin: 0 5%;
}

.section {
    margin: 0 5%;
}

.section.Main {
    width: 40%;
    border: solid black 1px;
    border-radius: 25px;
    margin: 30px 10px;
}

.flex {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.button {
    background-color: bisque;
    margin-right: 2px;
    font-size: 13px;
}

.rowDiv {
    margin: 5px;
}
.rowDiv .label {
    font-weight: bold;
    padding-right: 5px;
}

.rowDiv input {
}

a {
    text-decoration: none;
}
.UserComp {
    text-align: left;
    border: solid black 1px;
    border-radius: 10px;
    margin: 10px;
    padding: 10px;
}

.UserComp.userSelected {
    background-color: orange;
}
.UserComp.completedTasks {
    border: solid green 2px;
}

.UserComp.uncompletedTasks {
    border: solid red 2px;
}

.UserComp .rowDiv.name .label,
.UserComp .rowDiv.email .label {
    display: inline-block;
    width: 50px;
}

.UserComp .otherDataDiv .rowDiv .label{
    display: inline-block;
    width: 60px;
}

.UserComp .otherDataButton {
     margin-right: 10px;
    background-color: rosybrown;
 }

.UserComp .otherDataDiv {
    background-color: #F9F9F9;
    border: solid black 1px;
    border-radius: 10px;
    margin: 5px;
    padding: 5px;
}
.ShowUsers {
    margin: 0 25%;
}

.ShowUsers .addUserSpan {
    margin: 0 3px;
}
.TodosAndPosts .TodoComp,
.TodosAndPosts .PostComp {
    text-align: left;
    border: solid black 1px;
    margin: 10px;
    padding: 10px;
}

.TodosAndPosts .rowDiv .label {
    font-weight: bold;
    padding-right: 5px;
}

.TodosAndPosts .rowDiv .content {
    max-width: 70px;
    white-space: pre-wrap;
    word-break: break-word;
}
.markCompletedButtonSpan {
    float: right;
}
.UserTodos {
    text-align: left;
    margin: 10px;
    padding: 10px;
}

.UserTodosHeader{
    min-width: 400px;
    min-height: 30px;
}

.UserTodosHeader .addTodoButton {
    float: right;
    position: relative;
    bottom: 42px;
    right: 41px;
}

.UserTodosBody {
    min-width: 400px;
    min-height: 100px;
    max-height: 380px;
    overflow: scroll;
    margin: 5px;
    padding: 5px;
    border: solid black 2px;
}

.UserPosts {
    text-align: left;
    margin: 10px;
    padding: 10px;
}

.UserPostsHeader {
    min-width: 400px;
    min-height: 30px;
}

.UserPostsHeader .addPostButton {
    float: right;
    position: relative;
    bottom: 42px;
    right: 41px;
}

.UserPostsBody {
    min-width: 400px;
    min-height: 100px;
    max-height: 380px;
    overflow: scroll;
    margin: 5px;
    padding: 5px;
    border: solid black 2px;
}
.AddTodo {
    width: 50%;
    text-align: left;
    margin: 10px;
    padding: 10px;
}

.AddTodoBody {
    margin: 5px;
    padding: 20px;
    border: solid black 2px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.AddTodoBody .rowDiv {
    margin: 10px 0;
}

.AddTodoBody .rowDiv span {
    margin: 10px;
}

.AddTodoBody .rowDiv.title span {
    margin: 10px;
}

.AddTodoBody .rowDiv.title.titleError input {
    border: 1px solid red;
}
.AddPost {
    width: 50%;
    text-align: left;
    margin: 10px;
    padding: 10px;
}

.AddPostBody {
    margin: 5px;
    padding: 20px;
    border: solid black 2px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.AddPostBody .rowDiv {
    margin: 10px 0;
}

.AddPostBody .rowDiv span {
    margin: 10px;
}

.AddPostBody .rowDiv.title span,
.AddPostBody .rowDiv.body span {
    margin: 10px;
}

.AddPostBody .rowDiv.title.titleError input,
.AddPostBody .rowDiv.body.bodyError input {
    border: 1px solid red;
}
.AddUser {
    text-align: left;
    margin: 10px;
    padding: 10px;
}

.AddUserBody {
    margin: 5px;
    padding: 20px;
    border: solid black 2px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.AddUserBody .rowDiv {
    margin: 10px 0;
}

.AddUserBody .rowDiv span {
    margin: 10px;
}

.AddUserBody .rowDiv.name span,
.AddUserBody .rowDiv.email span {
    margin: 10px;
}

.AddUserBody .rowDiv.name.nameError input,
.AddUserBody .rowDiv.email.emailError input {
    border: 1px solid red;
}
.SearchFilter {
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 10px 0;
}

.SearchFilter .rowDiv input[name="searchString"]  {
    margin-right: 7px;
}
